/* Стилі для розгортаємої інструкції */
.instructions-container {
  margin-bottom: 30px;
}

.instructions-toggle {
  display: flex;
  align-items: center;
  gap: 8px;
  background: none;
  border: none;
  padding: 8px;
  color: #495057;
  font-size: 16px;
  cursor: pointer;
  transition: color 0.2s;
}

.instructions-toggle:hover {
  color: #212529;
}

.instructions-icon {
  color: currentColor;
}

.arrow-icon {
  color: currentColor;
  transform: rotate(0deg);
  transition: transform 0.3s ease;
}

.instructions-container.open .arrow-icon {
  transform: rotate(180deg);
}

.instructions-content {
  max-height: 0;
  overflow: hidden;
  transition: max-height 0.3s ease-out, padding 0.3s ease;
  background: #f8f9fa;
  border-radius: 4px;
}

.instructions-container.open .instructions-content {
  max-height: 500px;
  padding: 20px;
  margin-top: 10px;
  border: 1px solid #e9ecef;
}

.instructions-content ol {
  margin: 0;
  padding-left: 20px;
  color: #495057;
}

.instructions-content li {
  margin-bottom: 8px;
  line-height: 1.5;
}

.instructions-content li:last-child {
  margin-bottom: 0;
}

/* Стилі для футера */
